import axios from 'axios';

export const loadExercises = (userId, date) => {
    const basePath = 'exercises';
    return axios.get(basePath);
};

export const loadSets = () => {
    let newDate = new Date()
    const formattedDate = newDate.toISOString().split('T')[0]
    const basePath = `training/1/${formattedDate}`;
    return axios.get(basePath);
};

export const saveExerciseSet = (exerciseName, weight, reps, comment) => {
    const dto = makeDTO(exerciseName, weight, reps, comment);
    console.log(dto);
    const basePath = 'training';
    return axios.post(basePath, dto);
}

function makeDTO(exerciseName, weight, reps, comment) {
    return {
        exerciseName: exerciseName, userId: "1", weight: weight, reps: reps, comment: comment
    };
}
