import React, { useState, useEffect } from 'react';
import * as apiCalls from '../api/apiCalls';
import { Typeahead } from 'react-bootstrap-typeahead'; // ES2015
import 'react-bootstrap-typeahead/css/Typeahead.css';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

const SetForm = () => {
    const [list, setList] = useState([]);
    const [exerciseName, setExerciseName] = useState();
    const [weight, setWeight] = useState();
    const [reps, setReps] = useState();
    const [comment, setComment] = useState();

    useEffect(() => {
        const loadExercises = () => {
            apiCalls.loadExercises().then(response => {
                setList(response.data);
            })
        };
        loadExercises();
    }, []);

    const saveExerciseSet = () => {
        apiCalls.saveExerciseSet(exerciseName, weight, reps, comment).then(response => {
            console.log(response.data)
        })
    };

    const handleSubmit = () => {
        saveExerciseSet();
    }


    return (
        <Form onSubmit={handleSubmit}>
            <h5>Dodaj set</h5>
            <Form.Group className="mb-3" controlId="formBasicEmail">
                {<Typeahead
                    id="custom-selections-example"
                    allowNew={true}
                    options={list.map(exercise => exercise.exerciseName)}
                    placeholder="Ćwiczenie"
                    onChange={(e) => {
                        let text = e[0] instanceof Object ? e[0].label : e[0]
                        setExerciseName(text)
                    }}
                />}
            </Form.Group>

            <Form.Group className="mb-3">
                {/* <Form.Label>Email address</Form.Label> */}
                <Form.Control type="number" placeholder="Ciężar" onChange={(e) => setWeight(e.target.value)} />
            </Form.Group>

            <Form.Group className="mb-3">
                {/* <Form.Label>Powtórzenia</Form.Label> */}
                <Form.Control type="number" placeholder="Powtórzenia" onChange={(e) => setReps(e.target.value)} />
            </Form.Group>

            <Form.Group className="mb-3">
                {/* <Form.Label>Powtórzenia</Form.Label> */}
                <Form.Control type="text" placeholder="Uwagi" onChange={(e) => setComment(e.target.value)} />
            </Form.Group>

            <Button variant="primary" type="submit">
                Dodaj
            </Button>
        </Form>
    )
}

export default SetForm;